@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

body,
h1,
h2,
h3,
h4,
p,
li {
  margin: 0;
}

html {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

a {
  text-decoration: none;
  font-size: 2em;
}
a:visited {
  color: inherit;
}

i {
  color: #ffffff;
  transition: all 0.3s;
  font-size: 1.5em;
  pointer-events: none;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@font-face {
  font-family: Inter-Regular;
  src: url(./../font/Inter-Regular.ttf);
}
@font-face {
  font-family: Inter-Medium;
  src: url(./../font/Inter-Medium.ttf);
}
@font-face {
  font-family: Inter-Bold;
  src: url(./../font/Inter-Bold.ttf);
}
.main-font_regular {
  font-family: Inter-Regular, sans-serif;
}

.main-font_medium {
  font-family: Inter-Medium, sans-serif;
}

.main-font_bold {
  font-family: Inter-Bold, sans-serif;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #F1FAEE;
}

.header {
  width: 100%;
}
.header__background {
  background-color: #105610;
  text-align: center;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__title {
  display: block;
  font-size: 22px;
  color: #ffffff;
  padding: 0px 30px;
  text-decoration: underline;
}
@media (max-width: 768px) {
  .header__title {
    font-size: 18px;
    padding: 0px 20px;
  }
}
@media (max-width: 576px) {
  .header__title {
    font-size: 16px;
    padding: 0px 10px;
  }
}

main {
  flex: 1 1 auto;
  width: 100%;
}

.main__container {
  height: 100%;
  background-repeat: repeat;
  background-size: 25%;
  padding: 30px 20px;
}
@media (max-width: 1200px) {
  .main__container {
    padding: 20px 15px;
  }
}

.footer {
  width: 100%;
}
.footer__background {
  background-color: #105610;
  text-align: center;
  padding: 10px;
}
.footer__copyright {
  font-size: 16px;
  color: #ffffff;
}

/* Customize the scrollbar appearance */
::-webkit-scrollbar {
  -webkit-appearance: none;
}

/* Vertical scrollbar style */
::-webkit-scrollbar:vertical {
  width: 12px; /* Adjust the width as needed */
}

/* Horizontal scrollbar style (optional) */
::-webkit-scrollbar:horizontal {
  height: 12px; /* Adjust the height as needed */
}

/* For Firefox */
html {
  scrollbar-color: #96dd13 #F1FAEE;
}

body {
  scrollbar-color: #96dd13 #F1FAEE;
}

body::-webkit-scrollbar-track {
  background: #F1FAEE;
}

body::-webkit-scrollbar-thumb {
  background-color: #96dd13;
}

/* For WebKit (Chrome, Safari) */
::-webkit-scrollbar {
  background: #F1FAEE;
}

::-webkit-scrollbar-track {
  background: #F1FAEE;
}

::-webkit-scrollbar-thumb {
  background-color: #96dd13;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

.deck-of-cards-modal {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.36);
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 100;
}
.deck-of-cards-modal[data-open=true] {
  display: flex;
}
.deck-of-cards-modal__container {
  background-color: #d7f6da;
  width: 90%;
  height: 100%;
  border-radius: 25px 25px 25px 25px;
  box-shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);
  padding: 15px 25px 15px 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow-y: hidden;
  position: relative;
}
@media (max-width: 992px) {
  .deck-of-cards-modal__container {
    width: 95%;
  }
}
.deck-of-cards-modal__header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 20px;
}
.deck-of-cards-modal__title {
  font-size: 34px;
  font-weight: 700;
}
@media (max-width: 768px) {
  .deck-of-cards-modal__title {
    font-size: 20px;
  }
}
@media (max-width: 576px) {
  .deck-of-cards-modal__title {
    font-size: 18px;
  }
}
.deck-of-cards-modal__button_close {
  position: absolute;
  right: 45px;
  font-size: 20px;
  width: 40px;
  height: 40px;
  text-align: center;
  vertical-align: middle;
  color: white;
  background-color: black;
  text-decoration: none;
  border: none;
  border: 1px solid #343641;
  padding: 5px;
  border-radius: 100%;
  cursor: pointer;
  transition: ease 0.6s;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}
.deck-of-cards-modal__button_close:hover {
  width: 42px;
  height: 42px;
}
@media (max-width: 768px) {
  .deck-of-cards-modal__button_close {
    position: unset;
    margin-left: 10px;
  }
}
.deck-of-cards-modal__scroll-wrapper {
  overflow-y: auto;
}
.deck-of-cards-modal__decks-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  padding: 35px 0px 45px 0px;
}
.deck-of-cards-modal__decks-list div {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  max-width: 400px;
  margin: 20px 10px 0px 10px;
  background-color: white;
  padding: 20px;
  border-radius: 25px;
  flex: 1 auto;
}
@media (max-width: 576px) {
  .deck-of-cards-modal__decks-list div {
    padding: 20px 10px;
  }
}
.deck-of-cards-modal__decks-list img {
  max-width: 225px;
  width: 100%;
  height: auto;
}
.deck-of-cards-modal__decks-list .header {
  margin-top: 20px;
  font-size: 20px;
  text-align: left;
  font-family: Inter-Regular, sans-serif;
  font-weight: bold;
  max-width: 300px;
  text-align: center;
  width: 100%;
}
@media (max-width: 576px) {
  .deck-of-cards-modal__decks-list .header {
    font-size: 18px;
  }
}
.deck-of-cards-modal__decks-list .paragraph {
  margin-top: 15px;
  font-size: 18px;
  text-align: left;
  font-family: Inter-Regular, sans-serif;
  max-width: 300px;
  margin-bottom: 25px;
  text-align: center;
  width: 100%;
}
@media (max-width: 576px) {
  .deck-of-cards-modal__decks-list .paragraph {
    font-size: 16px;
  }
}
.deck-of-cards-modal__decks-list button {
  margin-top: auto;
  font-size: 18px;
  color: white;
  background-color: black;
  text-decoration: none;
  border: none;
  border: 1px solid #343641;
  padding: 5px 35px;
  border-radius: 30px;
  cursor: pointer;
  transition: ease 0.6s;
  user-select: none;
}
.deck-of-cards-modal__decks-list button:hover {
  transform: scale(1.05);
}

.cards-desk {
  width: 100%;
  background-image: url("https://arttherapyshopbydaniela.github.io/Service-MAC/dist/img/chalkboard.jpg");
  border-radius: 15px;
  background-repeat: repeat-x;
  background-size: cover;
  padding: 15px;
  position: relative;
}
.cards-desk .fullscreen-toggle-button {
  display: none;
  position: absolute;
  bottom: 40px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid #343641;
  padding: 5px 12px;
  border-radius: 11px;
  background-color: #ffffff;
  transition: ease 0.5s;
  opacity: 0.45;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  user-select: none;
}
@media (max-width: 576px) {
  .cards-desk .fullscreen-toggle-button {
    max-width: 300px;
  }
}
.cards-desk .fullscreen-toggle-button.show {
  display: flex;
}
.cards-desk .fullscreen-toggle-button, .cards-desk .fullscreen-toggle-button > * {
  cursor: pointer;
}
.cards-desk .fullscreen-toggle-button:hover {
  opacity: 1;
  transform: translateX(-50%) scale(1.05);
}
.cards-desk .fullscreen-toggle-button button {
  font-size: 20px;
  color: #343641;
  background-color: inherit;
  text-decoration: none;
  border: none;
  white-space: nowrap;
}
.cards-desk .fullscreen-toggle-button svg {
  color: #343641;
  height: 25px;
  transform: translateY(2px);
}
.cards-desk__scroll-view {
  overflow-y: auto;
  overflow-x: auto;
  padding-bottom: 20px;
  position: relative;
}
.cards-desk__scroll-view.fullscreen .cards-desk__container {
  min-height: 1000px;
  min-width: 1200px;
}
.cards-desk__scroll-status {
  display: block;
  position: absolute;
  font-family: sans-serif;
  font-size: 14px;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 0px 6px;
  border-radius: 5px;
  bottom: 20px;
  right: 40px;
  color: #96dd13;
  z-index: 10;
  user-select: none;
  animation-name: cssAnimation;
  animation-duration: 5s;
  animation-timing-function: ease-in;
  animation-direction: normal;
  animation-iteration-count: infinite;
  animation-fill-mode: reverse;
}
@keyframes cssAnimation {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.cards-desk__container {
  min-width: 600px;
  width: 100%;
  height: 100%;
  position: relative;
  min-height: 225px;
  box-sizing: content-box;
}
.cards-desk__container div {
  box-sizing: border-box;
  width: 150px;
  height: 210px;
  padding: 10px;
  border-radius: 10px;
  margin: 0px 5px;
  background-color: white;
  position: absolute;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .cards-desk__container div {
    padding: 6.5px;
  }
}
.cards-desk__container span {
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 3px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.panel {
  margin-top: 20px;
  width: 100%;
  border-radius: 5px;
  padding: 15px;
  background-image: url("https://arttherapyshopbydaniela.github.io/Service-MAC/dist/img/chalkboard.jpg");
  background-repeat: repeat-x;
  background-size: cover;
}
.panel__action {
  display: flex;
  justify-content: center;
  flex-direction: row;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 25px 15px 10px 15px;
  border-radius: 4px;
  flex-wrap: wrap;
}
@media (max-width: 992px) {
  .panel__action {
    padding: 25px 10px 5px 10px;
  }
}
@media (max-width: 768px) {
  .panel__action {
    flex-direction: column;
    align-items: center;
    padding: 25px 15px 5px 15px;
  }
}
.panel__action .couple-button {
  display: flex;
  flex-direction: row;
  background-color: transparent;
  border: none;
  user-select: none;
}
.panel__action .couple-button > * {
  cursor: pointer;
}
@media (max-width: 768px) {
  .panel__action .couple-button {
    width: 100%;
  }
}
@media (max-width: 576px) {
  .panel__action .couple-button {
    flex-direction: column;
    align-items: center;
  }
}
.panel__action .couple-button div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid #343641;
  padding: 5px 12px;
  margin: 0px 15px 15px 0px;
  border-radius: 11px;
  background-color: #ffffff;
  transition: ease-in-out 0.5s;
}
.panel__action .couple-button div.hide {
  display: none;
}
@media (max-width: 992px) {
  .panel__action .couple-button div {
    margin: 0px 10px 10px 0px;
  }
}
@media (max-width: 768px) {
  .panel__action .couple-button div {
    max-width: 100%;
    width: 100%;
  }
}
.panel__action .couple-button div:hover {
  transform: scale(1.05);
}
.panel__action .couple-button div:hover, .panel__action .couple-button div:hover > svg {
  transform: translateY(0.5px) scale(1.05);
}
.panel__action .couple-button button {
  font-size: 20px;
  color: #343641;
  background-color: inherit;
  text-decoration: none;
  border: none;
  user-select: none;
  white-space: nowrap;
  cursor: pointer;
}
.panel__action .couple-button svg {
  color: #343641;
  height: 25px;
  transition: ease-in-out 0.5s;
}
.panel__cards-line {
  width: 100%;
  padding: 35px 0px 20px 0px;
  display: flex;
  justify-content: left;
  flex-direction: row;
  overflow-y: hidden;
  overflow-x: scroll;
  flex-wrap: nowrap;
  min-height: 245px;
  box-sizing: border-box;
  perspective: 900px;
}
.panel__cards-line div {
  box-sizing: border-box;
  width: 150px;
  padding: 10px;
  border-radius: 10px;
  margin: 0px 5px;
  background-color: white;
  position: relative;
  transform-style: preserve-3d;
  transition: ease-out 1s;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.panel__cards-line div::before {
  backface-visibility: hidden;
  cursor: pointer;
}
.panel__cards-line div::before {
  content: "";
  display: inline;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  background-image: url("https://arttherapyshopbydaniela.github.io/Service-MAC/dist/img/card-patern.jpg");
  background-repeat: repeat;
  background-position: center;
  background-size: 200px;
  position: absolute;
  left: 0px;
  top: 0px;
  border-radius: 10px;
  transform: rotateY(180deg);
}
.panel__cards-line div[data-flip=true] {
  transform: rotateY(180deg);
}
.panel__cards-line span {
  display: block;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 130px;
  height: 190px;
  border-radius: 3px;
  overflow: hidden;
}

.draggable {
  touch-action: none;
  user-select: none;
  transform: translate(0px, 0px);
}